.reveal{
    opacity: 0;
}
.transformRight{
    animation: AnimeRightO 2s ease-in-out;
}
.transformLeft{
    animation: AnimeLeftO 2s ease-in-out;
}
.transformDroit{
    animation: AnimeLeft 2s ease-in-out;
}
.transformGauche{
    animation: AnimeRight 2s ease-in-out;
}
.fadeInUp {
    animation: fadeInUp 2s;
}
.ButtonVod img:last-child:hover{
    animation: Rotate 2s;
}
.ButtonVod img:hover{
    opacity: 1;
    transition: all 2s;
}
@keyframes AnimeLeft {
    0%{
        transform: translateX(-9%);
    }
    100%{
        transform: translateX(0%);
    }
}
@keyframes AnimeRight{
    0%{
        transform: translateX(9%);
    }
    100%{
        transform: translateX(0%);
    }
}
@keyframes AnimeLeftO {
    0%{
        opacity: 0;
        transform: translateX(-9%);
    }
    100%{
        transform: translateX(0%);
        opacity: 1;
    }
}
@keyframes AnimeRightO {
    0%{
        transform: translateX(9%);
        opacity: 0;
    }
    100%{
        transform: translateX(0%);
        opacity: 1;
    }
}
@keyframes fadeInUp {
    from {
        transform: translate3d(0,400px,0);
    }
    to {
        transform: translate3d(0,0,0);
        opacity: 1;
    }
}
@keyframes Rotate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}