.ArticleContaine{
    width: 100vw;
    height: fit-content;
    padding-top: 10vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}
.ArticleContaine::after{
    display: block;
    content: "";
    width: 90%;
    position: absolute;
    border-top: .4vh solid #141F29;
    right: 5%;
    bottom: 0%;
}
.ImageArticle{
    width: 100%;
    border-top: 4px solid #02A3CF;
}
.ArticleContaine h1, .ArticleContaine h2{
    margin: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.ArticleContaine h1{
    color: #FFFFFF;
    font-size: 5vh;
}
.ArticleContaine h2{
    color: #383D47;
    font-size: 3vh;
}
.LineArtcile{
    display: flex;
    width: 100vw;
    justify-content: space-between;
    align-items: center;
}
.ParagrapheText{
    background-color: #212f42;
    color: #FFFFFF;
    font-size: 2.6vh;
    width: 100%;
    position: relative;
    padding-right: 2vw;
    padding-top: 1.7vw;
    padding-left: 2.5%;
    padding-bottom: 1.5%;
    margin: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.ParagrapheText span{
    color: #02A3CF;
    font-weight: bolder;
}
.ParagrapheText::before{
    content: " ";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    border: 4px solid #02A3CF;
    right: 1.5vw;
    top: 1vw;
}
.ImgLine{
    width: 60vw;
    margin-left: 4%;
    position: relative;
}
.ImgLine img{
    position: relative;
    z-index: 1;
}
.ImgLine::before{
    content: " ";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #212f42;
    left: 2vw;
    bottom: 2vw;
}
.InnerLine{
    display: flex;
    margin-top: 1.5vw;
    margin-bottom: 2vw;
    margin-right: 2%;
    width: 25vw;
}
.InnerLine h1{
    margin: 0;
    writing-mode: tb-rl;
    transform: rotate(180deg);
    color: #383D47;
    align-self: center;
}
.LineArtcileDeux {
    width: 100vw;
    height: fit-content;
    display: flex;
    padding-top: 2%;
    padding-bottom: 2%;
}
.LineArtcileDeux .ParagrapheText{
    width: 17vw;
    margin-left: 4.7%;
    padding-top: 1.8vw;
    padding-left: 1.8vw;
}
.LineArtcileDeux .ParagrapheText::before{
    left: 1.2vw;
    top: 1.2vw;
}
.LastLine{
    width: 100%;
    height: fit-content;
    color: #383D47;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding-bottom: 2%;
    margin-top: 1%;
}
.LastLine p{
    font-size: 2.5vh;
    margin: 0;
    direction: rtl;
    margin-right: 3%;
    font-weight: bold;
}
.LinkBtn{
    align-self: flex-start;
    margin-left: 4%;
    justify-self: flex-start;
}
.BtnBlue{
    font-size: 2.4vh;
    background-color: #102337;
    color: #FFFFFF;
    border: 1px solid #00D8EC;
    margin-left: 5%;
    margin-top: 3%;
    padding: 5% 5% 5% 5%;
    cursor: pointer;
    width: max-content;
}
.LineArtcileDeux .ImgLine::before{
    display: none;
}
.LineArtcileDeux .ImgLine{
    width: 40vw;
    align-self: center;
    margin-left: 15%;
    border: none;
    position: relative;
}
.MainImage{
    width: 75%;
    margin-top: 2%;
}

@media (min-width : 320px) and (max-width : 750px) {
    .MainImage{
        width: 100%;
        margin-top: 0;
    }
    .MainImage h1{
        font-size: 3.5vh;
    }
    .ArticleContaine h2{
        font-size: 2.5vh;
    }
    .ImgLine{
        width: 100%;
    }
    .ImgLine::before{
        display: none;
    }
    .InnerLine{
        width: 85vw !important;
    }
    .InnerLine .ParagrapheText::before{
        top: 4vw;
        right: 4vw;
    }
    .InnerLine .ParagrapheText{
        padding-top: 4.5vw;
        padding-right: 4.7vw;
        font-size: 1.8vh;
    }
    .LineArtcileDeux .ImgLine{
        width: 80vw;
        margin-top: 5%;

    }
    .LineArtcileDeux .ParagrapheText{
        width: 80% !important;
        padding-top: 4.5vw;
        padding-left: 4vw;
        font-size: 1.8vh;
        margin-top: 2%;
    }
    .LineArtcileDeux .ParagrapheText::before{
        top: 3vw;
        left: 1.7vw;
    }
    .LastLine{
        flex-direction: column;
    }
    .LastLine p{
        font-size: 2vh;
    }
    .BtnBlue{
        align-self: flex-start;
        order: 2;
    }
}
@media (min-width : 750px) and (max-width : 900px) {
    .MainImage{
        width: 95%;
        margin-top: 1%;
    }
    .ImgLine{
        width: 90vw;
        margin-top: 2%;
        margin-bottom: 2%;
    }
    .InnerLine{
        width: 83vw !important;
    }
    .LineArtcileDeux .ImgLine{
        width: 70vw;
        margin-top: 4%;
        margin-bottom: 4%;
    }
    .LineArtcileDeux .ParagrapheText{
        width: 86% !important;
        margin-top: 2%;
        margin-bottom: 2%;
    }
}
@media (min-width : 900px) and (max-width : 1400px) {
    .ImgLine{
        width: 70vw;
    }
}
@media (max-width : 1400px) {
    .LineArtcile{
        flex-direction: column;
        margin-top: 4%;
    }
    .ImgLine{
        margin-left: 0;
    }
    .InnerLine{
        width: 60vw;
        margin-right: 0;
    }
    .LineArtcileDeux{
        flex-direction: column;
        align-items: center;
    }
    .LineArtcileDeux .ParagrapheText{
        width: 60%;
        margin-bottom: 3%;
        margin-left : 0;
    }
    .LineArtcileDeux .ImgLine{
        margin-left: 0;
        order: -1;
        margin-bottom: 2%;
    }
}
