.AboutComp{
    width: 100vw;
    height: fit-content;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.KamelCeoAbout{
    flex-direction: row;
}
.KamelCeoAbout .titleAboutContent{
    transform:  rotate(0deg);
    font-size: 7.5em;
}
.AboutComp .ShopText{
    opacity: .2;
    padding-top: 2%;
    margin-block-end: 0.57em;
}
.AboutComp .TextLine{
    border-bottom: 4px solid #02A3CF;
}
.ImgAbout{
    background-color: rgb(15, 54, 70, .5);
    border: 2.5px solid #114456;
    width: fit-content;
    padding: .7vw;
    position: relative;
    height: fit-content;
    max-width: 425px;
    max-width: 90vw;
}
.ImgAbout::after, .ImgAbout::before, .ImgAbout div,.ImgAbout .border{
    position: absolute;
    display: block;
    content: " ";
    width: 1.7vh;
    height: 1.7vh;
}
.ImgAbout::before{
    border-top: .5vh solid #02A3CF;
    border-right: .5vh solid #02A3CF;
    top: -2.5px;
    right: -2.5px;
}
.ImgAbout::after{
    border-top: .5vh solid #02A3CF;
    border-left: .5vh solid #02A3CF;
    top: -2.5px;
    left: -2.5px;
}
.Kamel .ImgAbout, .Prime .ImgAbout{
    padding: 0 0 0 0;
}
.CeoContainAbout{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.PrimeAbout .CeoContainAbout{
    align-self: flex-start;
}
.KamelCeoAbout .contentAboutText{
    padding: 0;
    padding-left: 2%;
    width: 58%;
}
.KamelCeoAbout .TitleImgAbout{
    margin-top: 0;
}
.KamelCeoAbout .ParagrapheContainAboute{
    align-self: center;
    position: relative;
}
.KamelCeoAbout .contentAboutText h5{
    align-self: flex-start;
    float: left;
    text-align: left;
    font-size: 4vh;
    width: fit-content;
}
.KamelCeoAbout .contentAboutText h5::before, .KamelCeoAbout .contentAboutText h5::after, .KamelCeoAbout .ParagrapheContainAboute::before, .KamelCeoAbout .ParagrapheContainAboute::after{
    display: none;
}
.KamelCeoAbout .ParagrapheContainAboute::before{
    content: ' ';
    top: 10px;
    left: -12px;
    width: 50%;
    height: 40%;
    position: absolute;
    display: block;
    border: none;
    border-left: 5px solid #02A3CF;
    z-index: 2;
}
.KamelCeoAbout .ParagrapheContainAboute::after{
    content: ' ';
    top: 10px;
    left: -11px;
    width: 75%;
    height: 75%;
    position: absolute;
    display: block;
    border: none;
    border-left: 3px solid #114456;
    z-index: 0;
}
.BarreRs{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 50%;
    margin-top: 5%;
    height: 25%;
    border-radius: 6px;
    background-color: #213042;
    border: 2.5px solid #114456;
}
.PrimeAbout{
    flex-direction: row-reverse;
}
.PrimeAbout .contentAboutText h5{
    width: fit-content;
    align-self: flex-start;
}
.PrimeAbout .contentAboutText h5::before, .PrimeAbout .contentAboutText h5::after, .PrimeAbout .ParagrapheContainAboute::before, .PrimeAbout .ParagrapheContainAboute::after{
    display: none;
}
.PrimeAbout .ParagrapheContainAboute::before{
    content: ' ';
    top: 10px;
    right: -12px;
    width: 101.5%;
    height: 45%;
    position: absolute;
    display: block;
    border: none;
    border-right: 5px solid #02A3CF;
    z-index: 2;
}
.PrimeAbout .ParagrapheContainAboute::after{
    content: ' ';
    top: 10px;
    right: -0;
    width: 101.7%;
    height: 85%;
    position: absolute;
    display: block;
    border: none;
    border-right: 3px solid #114456;
    z-index: 0;
}
.ContentSponsort .TitleImgAbout, .PartenaireAbout .TitleImgAbout{
    margin-top: 0;
}
.TextAboutSponsort{
    margin: 0;
    color: #FFFFFF;
    font-weight: bolder;
    width: 60%;
    padding-left: 2.5%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    font-size: 1.8vh;
}
.ChupaChupsAbout .TextAboutSponsort::before{
    content: ' ';
    bottom: -25px;
    right: 0;
    width: 60%;
    height: 85%;
    position: absolute;
    display: block;
    border: none;
    border-bottom: 3px solid #3D5980;
    z-index: 0;
}
.ChupaChupsAbout .TextAboutSponsort::after{
    content: ' ';
    bottom: -26px;
    right: 0;
    width: 30%;
    height: 85%;
    position: absolute;
    display: block;
    border: none;
    border-bottom: 5px solid #02A3CF;
    z-index: 2;
}
.LogitechsAbout .TextAboutSponsort::before{
    content: ' ';
    top: -30px;
    left: 25%;
    width: 55%;
    height: 100%;
    position: absolute;
    display: block;
    border: none;
    border-top: 3px solid #3D5980;
    z-index: 0;
}
.LogitechsAbout .TextAboutSponsort::after{
    content: ' ';
    top: -31px;
    left: 25%;
    width: 25%;
    height: 100%;
    position: absolute;
    display: block;
    border: none;
    border-top: 5px solid #02A3CF;
    z-index: 0;
}
.MichelinAbout .TextAboutSponsort::before{
    content: ' ';
    top: -30px;
    left: 25%;
    width: 55%;
    height: 100%;
    position: absolute;
    display: block;
    border: none;
    border-top: 3px solid #3D5980;
    z-index: 0;
}
.MichelinAbout .TextAboutSponsort::after{
    content: ' ';
    top: -31px;
    left: 25%;
    width: 25%;
    height: 100%;
    position: absolute;
    display: block;
    border: none;
    border-top: 5px solid #02A3CF;
    z-index: 0;
}
.LogitechsAbout .TextAboutSponsort{
    padding: 0;
    padding-right: 5%;
    padding-left: 2.5%;
    position: relative;
}
.RedBullAbout .TextAboutPartenaire{
    position: relative;
    font-size: 1.8vh;
}
.RedBullAbout .TextAboutPartenaire::before{
    content: ' ';
    top: -25px;
    right: 2%;
    width: 35%;
    height: 40%;
    position: absolute;
    display: block;
    border: none;
    border-top: 3px solid #3D5980;
    border-right: 3px solid #3D5980;
    z-index: 0;
}
.RedBullAbout .TextAboutPartenaire::after{
    content: ' ';
    top: -26px;
    right: 1.9%;
    width: 15%;
    height: 18%;
    position: absolute;
    display: block;
    border: none;
    border-top: 5px solid #02A3CF;
    border-right: 5px solid #02A3CF;
    z-index: 1;
}
.TextAboutPartenaire{
    width: 75%;
    padding-left: 2.5%;
    padding-right: 2.5%;
    color: #FFFFFF;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bolder;
    text-align: center;
    padding-left: 2.5%;
    margin-top: 5%;
}
.TextAboutPartenaire span{
    color: #02A3CF;
}
.TextAboutSponsort span{
    color: #02A3CF;
}
.Sponsort{
    text-align: end;
    margin-block-start: 0;
}
.TitleImgAbout{
    display: flex;
    align-items: center;
    align-self: flex-start;
    margin-right: 5%;
    margin-top: 6%;
}
.TitleImgAbout h2{
    color: #FFFFFF;
    border-bottom: 3px solid #114456;
    width: 21vw;
    min-width: fit-content;
    font-size: 5vh;
    padding-bottom: .2%;
}
.TitleImgAbout img{
    width: 100%;
    max-width: 230px;
    min-width: 130px;
}
.ContentAbout{
    width: 100%;
    display: flex;
    height: 90%;
}
.contentAboutText{
    width: 60%;
    height: 100%;
    margin: 0;
    font-size: 1.8vh;
    display: flex;
    align-items: center;
    position: relative;
    padding-right: 3%;
}
.ParagrapheContainAboute::before{
    content: ' ';
    bottom: -14px;
    left: 1px;
    width: 50%;
    height: 10%;
    border-bottom: 3px solid #3D5980;
    border-left: 3px solid #3D5980;
    position: absolute;
}
.ParagrapheContainAboute::after{
    content: ' ';
    bottom: -15px;
    left: 0;
    width: 30%;
    height: 5%;
    border-bottom: 5px solid #02A3CF;
    border-left: 5px solid #02A3CF;
    position: absolute;
}
.titleAboutContent{
    text-shadow: 2px 0 0 #02A3CF, -2px 0 0 #02A3CF, 0 2px 0 #02A3CF, 0 -2px 0 #02A3CF, 1px 1px #02A3CF, -1px -1px 0 #02A3CF, 1px -1px 0 #02A3CF, -1px 1px 0 #02A3CF;
    font-size: 8.5em;
    color: #101720;
    writing-mode:vertical-rl;
    transform: rotate(180deg);
    margin: 0;
}
.contentAboutText h1, h5, p{
    margin: 0;
}
.ParagrapheContainAboute{
    display: flex;
    flex-direction: column;
    align-items: center;
    align-self: flex-start;
    margin-top: 3%;
    position: relative;
}
.contentAboutText h5{
    color: #FFFFFF;
    font-size: 5vh;
    width: 40%;
    text-align: right;
    align-self: flex-end;
    position: relative;
}
.contentAboutText h5::before{
    bottom: -7px;
    right: 0;
    display: block;
    position: absolute;
    content: " ";
    width: 120%;
    height: 3px;
    background-color: #3D5980;
}
.contentAboutText h5::after{
    bottom: -8px;
    right: 0;
    display: block;
    position: absolute;
    content: " ";
    width: 60%;
    height: 5px;
    background-color: #02A3CF;
}
.contentAboutText p{
    font-size: 1.9vh;
    text-align: center;
    color: #FFFFFF;
    font-weight: bolder;
}
.contentAboutText p span{
    color: #02A3CF;
}
.Kamel{
    margin-left: 3.2%;
    margin-right: 0;
    margin-top: 3.2%;
}
.Prime{
    align-self: flex-end;
    margin-top: 3.2%;
}
.Kamel h2, .Prime h2, .ChupaChupsAbout h2, .LogitechsAbout h2, .RedBullAbout h2{
    font-size: 3.6vh;
}
.ChupaChupsAbout h2, .LogitechsAbout h2{
    width: 9vw;
    padding-bottom: .6%;
}
.Kamel span, .Prime span, .ChupaChupsAbout span, .LogitechsAbout span, .RedBullAbout span{
    color: #02A3CF;
}
.Kamel img{
    max-width: 340px;
    min-width: 170px;
}
.Prime img{
    max-width: 425px;
    min-width: 170px;
}
.ChupaChupsAbout img{
    max-width: 425px;
    min-width: 210px;
    width: 100%;
}
.LogitechsAbout img{
    max-width: 250px;
    min-width: 210px;
    width: 100%;
}
.RedBullAbout img{
    max-width: 399px;
    min-width: 210px;
    width: 100%;
}
.ContentSponsort{
    width: 100%;
    /* height: 100%; */
    height: fit-content;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
}
.ChupaChupsAbout .ImgAbout{
    margin-left: 3vw;
    margin-top: 2vw;
}
.LogitechsAbout .ImgAbout{
    align-self: flex-end;
    justify-self: flex-end;
    margin-right: 4vw;
    margin-bottom: 3vw;
}
.RedBullAbout .ImgAbout{
    align-self: flex-start;
    margin-left: 3vw;
    margin-top: 3.7vw;
}
.RedBullAbout, .ChupaChupsAbout{
    margin-right: 0;
}
.RedBullAbout h2{
    width: 11vw;
}
.TitleImgAbout h2{
    width: fit-content;
}
.LogitechsAbout{
    justify-self: flex-end;
    margin-right: 0;
    margin-bottom: 2.2%;
}
.PartenaireAbout{
    position: relative;
    height:69vh;
}
.PartenaireAbout::after{
    display: block;
    content: "";
    width: 90%;
    position: absolute;
    border-top: .4vh solid #141F29;
    right: 5%;
    bottom: 0%;
}
.ImgFlex{
    display: flex;
    align-items: center;
}
@media (max-width : 600px){
    .RedBullAbout .TextAboutPartenaire::after, .RedBullAbout .TextAboutPartenaire::before, .PrimeAbout .ParagrapheContainAboute::before, .PrimeAbout .ParagrapheContainAboute::after, .KamelCeoAbout .ParagrapheContainAboute::before, .KamelCeoAbout .ParagrapheContainAboute::after, .ParagrapheContainAboute::before, .ParagrapheContainAboute::after, .contentAboutText h5::before, .contentAboutText h5::after{
        display: none;
    }
    .contentAboutText h5{
        width: 100% !important;
    }
    .contentAboutText{
        flex-direction: column-reverse;
    }
    .titleAboutContent{
        writing-mode: horizontal-tb;
        transform: rotate(0deg);
    }
    .TitleImgAbout{
        flex-direction: column-reverse;
    }
    .Kamel{
        flex-direction: column;
    }
    .Kamel h2{
        width: 100%;
    }
    .KamelCeoAbout .contentAboutText{
        flex-direction: column;
    }
    .titleAboutContent{
        font-size: 6em;
    }
    .ImgFlex{
        flex-direction: column;
    }
    .LogitechsAbout .ImgFlex{
        flex-direction: column-reverse;
    }
    .LogitechsAbout .ImgAbout{
        margin-bottom: 0;
        margin-right: 0;
    }
    .AboutComp .ShopText{
        padding-top: 5%;
    }
    .AboutComp .Sponsort{
        display: block !important;
    }
    .PartenaireAbout .ShopText{
        text-align: start;
    }
    .LogitechsAbout .TextAboutSponsort, .TextAboutPartenaire, .TextAboutSponsort , .contentAboutText{
        padding: 0;
    }
    .TitleImgAbout, .ChupaChupsAbout .ImgAbout, .RedBullAbout .ImgAbout{
        margin: 0;
    }
}
@media (max-width : 1200px){
    .ContentAbout{
        flex-direction: column-reverse;
        align-items: center;
    }
    .TitleImgAbout{
        align-self: center;
    }
    .AboutComp{
        flex-direction: column;
    }
    .KamelCeoAbout{
        margin-top: 3%;
    }
    .PrimeAbout .CeoContainAbout{
        align-self: center;
    }
    .RedBullAbout, .ChupaChupsAbout{
        flex-direction: column;
    }
    .LogitechsAbout{
        flex-direction: column-reverse;
    }
    .LogitechsAbout .ImgAbout{
        align-self: center;
    }
    .LogitechsAbout .TextAboutSponsort::before, .LogitechsAbout .TextAboutSponsort::after, .MichelinAbout .TextAboutSponsort::before, .MichelinAbout .TextAboutSponsort::after{
        border: none;
    }
    .TextAboutSponsort{
        margin-bottom: 3%;
    }
    .TextAboutSponsort{
        width: 90%;
    }
    .RedBullAbout .TextAboutPartenaire{
        margin-bottom: 2%;
        width: 90%;
    }
    .contentAboutText{
        width: 90%;
    }
    .KamelCeoAbout .contentAboutText{
        width: 90%;
    }
    .TitleImgAbout{
        margin-top: 2%;
    }
}
@media (max-width : 1500px){
    .AboutComp{
        height: fit-content;
    }
    .contentAboutText h5{
        width: 60%;
    }
}