@import url('https://fonts.googleapis.com/css2?family=Rock+Salt&display=swap');


.MatchContent{
    width: 100vw;
    height: 90vh;
    padding-top: 10vh;
    background-color: #101720;
    background-image: url('../../Image/Headers/Kc.svg');
    background-position: -40vh -1vh;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    justify-content: space-between;
}
.Countdown{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    width: 35%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.Countdown h1{
    font-size: 5vh;
    color: #ffffff;
    border-bottom: 2px solid #2A76A7;
}
.Countdown span{
    color: #12DCFC;
}
.CountDownContain{
    margin-bottom: 20%;
    display: flex;
    width: 74%;
    justify-content: space-between;
}
.TwitchChannelMatch{
    border: none !important;
    font-size: 4vh !important;
    background-color: #172030;
    padding: 1% 0% 1.7% 0%;
    width: 83%;
    text-align: center;
}
.CountDownContain i{
    width: 3px;
    height: 100%;
    background-color: #3D5980;
}
.CountDownContain div{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: bolder;
}
.CountDownContain span{
    font-size: 6vh;
    color: #ffffff;
}
.CountDownContain h2{
    font-size: 3vh;
    color: #0C3F54;
}
.MatchContentUpcomingy{
    display: flex;
    height: 85%;
    align-items: center;
    justify-content: space-evenly;
    grid-column: 2;
    width: 65vw;
}
.HistoriqueMatchFind{
    justify-content: initial;
}
.HitsoriqueMatch{
    background-color: #101720;
    position: relative;
    padding-bottom: 1%;
}
.HitsoriqueMatch::after{
    display: block;
    content: "";
    width: 90%;
    position: absolute;
    border-top: .4vh solid #141F29;
    right: 5%;
    bottom: 0%;
}
.Date{
    display: flex;
    width: 60%;
    align-items: center;
    justify-content: space-around;
    color: #ffffff;
}
.Autocomplite{
    position: absolute;
    list-style: none;
    background-color: rgb(15, 54, 70, .75);
    color: #ffffff;
    z-index: 1;
    top: 105%;
    width: 100%;
    left: 2%;
    margin: 0;
    padding: 0;
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    cursor: pointer;
    font-size: 1.7vh;
    max-height: 100px;
    overflow: scroll;
}
.Autocomplite li{
    padding: 3%;
}
.DateInput input::placeholder, .InputAdversaire input::placeholder, .InputJeux input::placeholder, .InputTournois input::placeholder{
    color: #878fa1;
}
.DateInput input, .InputAdversaire input, .InputJeux input, .InputTournois input{
    outline: none;
    border-radius: 4px;
    border: none;
    font-size: 2vh;
    width: 100%;
    padding-left: 3%;
    background-color: #2a3c53;
    border: 1px solid #455f81;
    color: #ffffff;
}
.InputAdversaire{
    width: 30%;
}
.Lane1 .InputAdversaire{
    min-width: 160px;
}
.Lane2 .Date{
    min-width: 285px;
}
.InputTournois{
    min-width: 100px;
}
.InputJeux{
    width: 43%;
}
.DateInput{
    width: 25%;
}
.InputTournois{
    width: 20%;
}
.Lane1{
    width: 100%;
    display: flex;
    justify-content: space-around;
}
.Lane2{
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin-top: 2%;
    margin-bottom: 2%;
}
.ButtonRecherche{
    width: fit-content;
    outline: none;
    border: none;
    border-radius: 5px;
    font-size: 2vh;
    align-self: center;
    margin-top: 1%;
    color: #ffffff;
    background-color: #0C3F54;
    border: 2px solid #101720;
    cursor: pointer;
    position: relative;
    padding: 1.5%;
}
.ButtonRecherche::before, .ButtonRecherche::after{
    content: " ";
    display: block;
    position: absolute;
    width: .6vh;
    height: .6vh;
}
.ButtonRecherche::before{
    border-bottom: 3px solid #12DCFC;
    border-left: 3px solid #12DCFC;
    bottom: -2px;
    left: -2px;
}.ButtonRecherche::after{
    border-top: 3px solid #12DCFC;
    border-right: 3px solid #12DCFC;
    top: -2px;
    right: -2px;
}
.RechercheMatch{
    margin-left: 3%;
    width: 27vw;
    height: fit-content;
}
.ButtonMatchFind{
    user-select: none;
    font-size:2.1vh;
    color : #ffffff;
    cursor:pointer; 
    width: 30%;
    text-align: center;
    padding: 1.4%;
    padding-left: 1.7%;
    border-radius: 10px;
    background-color: rgb(15, 54, 70, .5);
    border: 3px solid #0C3F54;
    position: relative;
    z-index: 4;
}
.FormContain{
    width: 100%;
    background-color: rgb(15, 54, 70, .5);
    border-bottom: 3px solid #0C3F54;
    border-left: 3px solid #0C3F54;
    border-right: 3px solid #0C3F54;
    border-radius: 0 6px 6px 6px;
    padding: 2%;
    display: flex;
    flex-direction: column;
    min-width: 375px;
    position: relative;
    z-index: 4;
}
.FormContain::before{
    position: absolute;
    content: " ";
    display: block;
    top: 0;
    width: 68.8%;
    height: 100%;
    border-top: 3px solid#0C3F54;
    border-radius: 0 6px 0 0; 
    right: -3px;
}
.MatchContent .ColoneLastMatch::before, .MatchContent .ColoneLastMatch::after,.MatchContent .ColoneNextMatch::after, .MatchContent .ColoneNextMatch::before, .HitsoriqueMatch .ColoneLastMatch::before, .HitsoriqueMatch .ColoneLastMatch::after, .HitsoriqueMatch .ColoneNextMatch::before, .HitsoriqueMatch .ColoneNextMatch::after{
    display: none;
}
.HitsoriqueMatch {
    flex-direction: row;
    position: relative;
}
.HitsoriqueMatch .MatchContaine{
    width: 100vw;
}
.MatchContent .MatchContaine{
    margin-top: 5%;
    margin-left: 0;
}
.TrophyStyleMatch{
    width: 63vw;
    height: 95%;
    position: absolute;
    z-index: 0;
    right: 3%;
}
.TrophyStyleMatch:hover .BlueWall, .MatchContaine:hover .BlueWall{
    top: 0%;
    right: 0%;
}
.TrophyStyleMatch:hover .GifContain, .MatchContaine:hover .GifContain{
    bottom: 0%;
    left: 0%;
}
.BlueWall{
    width: 52%;
    height: 60%;
    background-image: url("../../Image/Match/BlueWall.jpg");
    border: 2.7px solid #000000;
    position: absolute;
    top: 8%;
    right: 10%;
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.BlueWall h1{
    font-family: 'Rock Salt', cursive;
    margin: 0;
    margin-top: 3%;
    color: #101720;
}
.BlueWall span{
    color: #2A76A7;
}
.BlueWall span i{
    color: #cdcdcd;
}
.GifContain{
    width: 53%;
    max-height: 60%;
    height: fit-content;
    background-color: #1F1F1F;
    border: 2.7px solid #000000;
    position: absolute;
    bottom: 3%;
    left: 2%;
    overflow: hidden;
}
.GifContain h1, .GifContain h2{
    display: none;
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 0;
    color: #ffffff;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    cursor: pointer;
}
.GifContain h2{
    display: block;
    top: 0;
    left: 0;
}
.GifContain:hover h1{
    display: block;
    color: #12DCFC;
    font-size: 2.7vh;
}
.GifMatch{
    height: auto;
    width: 100%;
}
.HistoriqueMatchConayin{
    display: flex;
    justify-content: center;
}

@media (max-width: 544px){
    .InputAdversaire{
        margin-bottom: 2%;
    }
    .Date{
        margin-bottom: 2%;
    }
    .Lane1{
        flex-direction: column;
        align-items: center;
    }
    .Lane2{
        flex-wrap: wrap;
    }
    .RechercheMatch{
        width: 97% !important;
        align-self: flex-start;
    }
    .FormContain{
        width:100%;
        padding: 0;
        padding-top: 2%;
        min-width: 0;
    }
}
@media  (min-width: 544px) and (max-width : 670px) {
    .RechercheMatch{
        width: 75% !important;
    }
}
@media (max-width : 670px) {
    .Countdown{
        width: 95% !important;
    }
    .TrophyStyleMatch{
        width: 75%;
    }
    .RechercheMatch{
        margin: 0;
        margin-bottom: 4%;
        width: 100%;
    }
}
@media (min-width: 670px) and (max-width : 870px) {
    .Countdown{
        width: 80% !important;
    }
}
@media (max-width : 870px){
    .HistoriqueMatchFind{
        flex-direction: column;
        align-items: center;
    }
    .RechercheMatch{
        width: 60%;
    }
}
@media (min-width : 870px) and (max-width : 1400px){
    .HistoriqueMatchFind{
        flex-wrap: wrap;
        justify-content: center;
    }
}
@media (min-width : 870px) and (max-width : 1167px){
    .RechercheMatch{
        width: 40% !important;
    }
    .ButtonMatchFind{
        width: 40%;
    }
    .FormContain::before{
        width: 59.1%;
    }
}
@media  (max-width : 1400px) {
    .HitsoriqueMatch .ColoneNextMatch{
        width: 45vw;
        margin-bottom: 3%;
    }
    .BlueWall{
        display: none;
    }
    .TrophyStyleMatch ,.GifContain{
        position: relative;
    }
    .GifContain{
        width: 100%;
    }
    .CountDownContain i{
        height: auto !important;
    }
    .Countdown{
        width: 55%;
        order: -2;
    }
    .CountDownContain{
        margin-bottom: 2%;
    }
    .MatchContent{
        flex-direction: column-reverse;
        min-height: 90vh;
        height: fit-content;
        align-items: center;
    }
    .MatchContaine{
        width: 100%;
    }
    .CountDownContain{
        width: 97%;
    }
}
@media (min-width : 1400px) and (max-width : 1800px) {
    .CountDownContain{
        width: 90%;
    }
}