.Footer{
    height:20vh;
    width: 100vw;
    background-color: #101720;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display: grid;
    grid-template-columns: .2fr 120px .2fr 1fr .2fr 1fr .3fr ;
    align-items: center;
    padding-bottom: 1%;
}
.Colonne1{
    display: flex;
    flex-direction: column;
    grid-column: 4;
}
.Colonne1 h1, h2, a{
    margin: 0;
    text-decoration: none;
}
.Colonne1 h1{
    font-size: 3.5vh;
    color: #FFFFFF;
}
.Colonne1 h2{
    margin-top: 1%;
    font-size: 2.6vh;
    font-weight: 100;
    color: #5C5F69;
}
.Colonne1 h2:hover{
    color: #9c9fa8;
    transition: all 1.5s;
}
.Colonne2{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    grid-column: 6;
}
.Colonne2 h1{
    margin: 0;
    color: #FFFFFF;
    font-size: 3.2vh;
}
.Colonne2 span{
    position: relative;
    z-index: 4;
    color: #12DCFC;
}
.Colonne3{
    display: flex;
    align-items: center;
}
@media (min-width : 320px) and (max-width : 480px) {
    /* Styles pour tlphone */
    .Footer{
        grid-template-columns: auto;
    }
}