.WaitingBck{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #101720;
    overflow: hidden;
}
.WaitingBck h1{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #fff;
    font-size: 5vh;
    font-weight: bolder;
    margin: 0;
    margin-top: 1%;
    margin-bottom: 1%;
}
.StyleImgW{
    width: 15%;
    max-width: 200px;
    min-width: 100px;
}
.surround {
    width: fit-content;
    position: relative;
} 
.loading {
    display: flex;
    width: fit-content;
    height: 18px;
    padding: 0;
}  
.loading li {
    display: inline-block;
    width: 25px;
    height: 25px;
    margin-right: 6px;
    border-radius: 50%;
    border: 2px solid #fff;
    opacity: 0;
    box-sizing: border-box;
    animation-name: load;
    animation-duration: 3s;
    animation-iteration-count: infinite;
} 
.loading li:nth-child(2) {
    animation-delay: 0.4s;
}
.loading li:nth-child(3) {
    animation-delay: 0.8s;
}
@keyframes load {
    0% {
      transform: scale(0,0);
      opacity: 0;
      background-color: transparent;
    }
    25% {
      opacity: 0.8;
      background-color: #fff;
    }
    50% {
      opacity: 0.8;
      background-color: #fff;
    }
    70% {
      transform: scale(1,1);
      opacity: 0;
      background-color: transparent;
    }
    100% {
      opacity: 0;
    }
}