/**/
.AboutComponent{
  height: 90vh;
  width: 100vw;
  background:#101720;
  padding-top: 10vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
.ContaineTitleAbout::before, .AboutComponent::before, .AboutComponent::after, .ContaineTitleAbout h1::before, .DecoAbout, .ButtonAbout::before, .ButtonAbout::after, .DivContainTwitch{
  content: ' ';
  position: absolute;
  display: block;
}
.AboutComponent::before{
  z-index: 0;
  background-image: url('../Image/About/DrapeauA.png');
  background-repeat: no-repeat;
  background-size: 100%;
  max-width: 100vw;
  max-height: 100%;
  height: 100%;
  width: 100%;
  top: 0;
}
.AboutComponent::after{
  z-index: 1;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(#0a121d , #0C1119);
  opacity: .8;
  top: 0;
}
.ContaineTitleAbout h1{
  font-size: 7.2vw;
  color: #FFFFFF;
  margin: 0;
  position: relative;
  z-index: 2;
}
.ContaineTitleAbout span{
  color: #02A3CF;
}
.ContaineTitleAbout::before{
  width: 100%;
  height: 100%;
  opacity: .65;
  background-color: #101720;
  filter: blur(7px);
  top: 0;
}
.ContaineTitleAbout{
  position: relative;
  z-index: 2;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.ContaineTitleAbout p{
  color: #FFFFFF;
  width: 80%;
  margin: 0;
  margin-bottom: 2%;
  margin-top: 1%;
  text-align: center;
  font-size: 1.8vh;
  font-weight: bold;
}
.ParagrapheAbout{
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 70%;
  position: relative;
  z-index: 2;
}
.ButtonAbout{
  text-decoration: none;
  min-width: fit-content;
  font-size: 2.5vh;
  padding: 1%;
  background-color: rgb(15, 54, 70, .5);
  border: 2px solid #0C3F54;
  color: #FFFFFF;
  font-weight: bold;
  position: relative;
  padding-right: 1.9%;
}
.ButtonAbout span{
  color: #FFFFFF;
}
.ButtonAbout span:hover{
  border-bottom: 3px solid #02A3CF;
  color: #02A3CF;
  transition: color 2s;
  cursor: pointer;
}
.ButtonAbout::before{
  width: 1.2vh;
  height: 1.2vh;
  border-bottom: .5vh solid #02A3CF;
  border-left: .5vh solid #02A3CF;
  bottom: -2.5px;
  left: -2.5px;
}
.ButtonAbout::after{
  width: 1.2vh;
  height: 1.2vh;
  border-top: .5vh solid #02A3CF;
  border-right: .5vh solid #02A3CF;
  top: -2.5px;
  right: -2.5px;
}
/*------------------------------------------StyleComponentMain--------------------------------------------------*/
.StyleComponentMain{
  width: 100vw;
  height: fit-content;
  background-color: #101720;
  overflow: hidden;
  position: relative;
  margin-bottom: 2%;
}
.BarreScrollStyle{
  width: 100%;
  height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.BarreScrollStyle span{
  color: #FFFFFF;
  margin-top: .7%;
  margin-bottom: .4%;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 17px;
}
.BarreScrollStyle i{
  content: " ";
  width: 2px;
  height: 100%;
  background-color: #FFFFFF;
  position: relative;
  opacity: .8;
}
.BarreScrollStyle i::before{
  display: block;
  position: absolute;
  content: " ";
  width: 15px;
  height: 15px;
  background-color: #02A3CF;
  top: 45%;
  left: -6.5px;
  border-radius: 50%;
  opacity: .8;
}
.StyleComponentMain h1{
  text-shadow: 2px 0 0 #212a40, -2px 0 0 #212a40, 0 2px 0 #212a40, 0 -2px 0 #212a40, 1px 1px #212a40, -1px -1px 0 #212a40, 1px -1px 0 #212a40, -1px 1px 0 #212a40;
  color: #101720;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 11vh;
  width: 200%;
  margin: 0;
  margin-bottom: 1%;
  clear: both;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
}
.TwitchContainAbout{
  position: relative;
}
.DivContainTwitch{
  z-index: 5;
  width: 50%;
  height:13%;
  background-color: #172030;
  left: 25%;
  top: 44.5%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.DivContainTwitch h1{
  color: #FFFFFF;
  font-size: 4vh;
  width: fit-content;
}
.DivContainTwitch span{
  color: #02A3CF;
}
/*---------------------------------------News Components-----------------------------------------------------------*/
.TextLine{
  text-align: center;
  border-bottom: 5px solid #02A3CF;
  line-height: 0;
  width: 95%;
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: bold;
  font-size: 6vh;
}
.TextLine #Line{
  background: #101720;
  padding: 0 15px;
  width: fit-content;
  color: #FFFFFF;
}




.StyleNewsComp{
  position: absolute;
  right: 0;
  width: 18.7vw;
  height: 4vh;
}
.StyleNewsComp .StyleNewsBar1{
  position: absolute;
  width: 100%;
  height: 4px;
  background-color: #02A3CF;
}
.StyleNewsComp .StyleNewsBar2{
  position: absolute;
  width: 45%;
  height: 4px;
  background-color: #02A3CF;
  bottom: 0;
  right: 0;
}




.NewsComponent{
  width: 100vw;
  height: 85vh;
  background: #101720;
  position: relative;
  margin-bottom: 4%;
}
.NewsTitleComp{
  margin: 0;
  font-size: 10vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #FFFFFF;
  align-self: flex-start;
  margin-left: 2%;
  position: relative;
  width: auto;
}
.NewsTitleComp span{
  color: #02A3CF;
}
.NewsTitleComp::before{
  display: block;
  position: absolute;
  content: " ";
  bottom: 7%;
  width: 7vw;
  height: 4px;
  background-color: #02A3CF;
  left: 120%;
}
.NewsForm{
  width: 95vw;
  display: flex;
  justify-content: space-between;
  margin-top: 1vh;
}
.ParagrapheNews{
  width: 23%;
  height: fit-content;
  margin-top: 2%;
  position: relative;
}
.ParagrapheNews img{
  width: 25%;
  position: relative;
  margin-top: -12%;
}
.ParagrapheNews p{
  margin: 0;
  margin-top: 1%;
  margin-bottom: 3%;
  padding-left: 6%;
  padding-right: 2%;
  margin-right: 3%;
  border-right: 4px solid #02A3CF;
  font-size: 2.1vh;
  overflow: scroll;
}
.ParagrapheNews h1{
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: bold;
  font-size: 3.3vh;
  align-self: flex-start;
  margin-left: 6%;
  width: fit-content;
  border-top: 4px solid #02A3CF;
}

.FirstLayer{
  width: 100%;
  height: 100%;
  background-color: #3d5980;
  color: #FFFFFF;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
  max-height: 60vh;
  padding-bottom: 4%;
  border: 4px solid #1F4859;
}
.FirstLayer p{
  overflow-x: hidden;
}
.FirstLayer p::-webkit-scrollbar-track
{
	background-color: #3D5980;
}
.FirstLayer p::-webkit-scrollbar
{
	width: 7px;
	background-color: #F5F5F5;
}
.FirstLayer p::-webkit-scrollbar-thumb
{
	border-radius: 15px;
	background-color: #0a121d;
} 
.FirstLayer::after, .FirstLayer::before, .ParagrapheNews::after, .ParagrapheNews::before{
  display: block;
  content: "";
  width: 5%;
  height: 5%;
  position: absolute;
  z-index: 2;
}
.FirstLayer:after {
  bottom: 0px;
  right: -4px;
  border-bottom: 4px solid #02A3CF;
  border-right: 4px solid #02A3CF;
}
.FirstLayer::before {
  top: 0px;
  left: -4px;
  border-top: 4px solid #02A3CF;
  border-left: 4px solid #02A3CF;
}
.ParagrapheNews::before{
  border-top: 4px solid #02A3CF;
  border-right: 4px solid #02A3CF;
  top: 0;
  right: -4px;
}
.FirstLayer a{
  text-decoration: none;
  color: #FFFFFF;
  width: 100%;
}
.FirstLayer a:hover{
  color: #02A3CF;
  transition: all 2s;
}
.ParagrapheNews::after{
  border-bottom: 4px solid #02A3CF;
  border-left: 4px solid #02A3CF;
  bottom: 0;
  left: -4px;
}
.SecondLayer{
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgb(15, 54, 70, .5);
  border: 2px solid #0C3F54;
  margin-top: 5%;
  margin-right: 20%;
  z-index: 0;
}
/*--------------------------------------------Matchs components--------------------------------------------------------*/

