}

.typewriter-text {
  display: inline-block;
  overflow: hidden;
  letter-spacing: 2px;
 animation: typing 5s steps(30, end), blink .75s step-end infinite;
  white-space: nowrap;
  font-size: 7.5vh;
  text-shadow: 2px 0 0 #212a40, -2px 0 0 #212a40, 0 2px 0 #212a40, 0 -2px 0 #212a40, 1px 1px #212a40, -1px -1px 0 #212a40, 1px -1px 0 #212a40, -1px 1px 0 #212a40;
  color: #101720;
  font-weight: bolder;
  border-right: 2px solid #3D5980;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

@keyframes typing {
  from { 
      width: 0% 
  }
  to { 
      width: 100% 
  }
}

@keyframes blink {
  from, to { 
      border-color: transparent 
  }
  50% { 
      border-color: #3D5980; 
  }
}
/*--------------------------------------------Shop components----------------------------------------------------------*/
.ShopComponent{
  height: 70vh;
  width: 100vw;
  background-color: #101720;
  position: relative;
}
.ShopComponent::before{
  display: block;
  content: "";
  width: 90%;
  position: absolute;
  border-top:.4vh solid #141F29;
  right: 5%;
  bottom: 2%;
}
.ContaineShop{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.ShopTitle{
  color: #FFFFFF;
  font-size: 9vh;
  font-weight: bold;
  margin-bottom: 4%;
  position: relative;
}
.ShopTitle span{
  color: #02A3CF;
}
.ShopTitle::before{
  content: " ";
  display: block;
  position: absolute;
  width: 50%;
  height: 4px;
  background-color: #0A627D;
  top: -15%;
  left: 10%;
}
.ShopTitle::after{
  content: " ";
  display: block;
  position: absolute;
  width: 10%;
  height: 4px;
  background-color: #0A627D;
  top: -15%;
  right: 20%;
}
.ContaineTextShop{
  width: 31%;
  height: 100%;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-left: 2%;
}
.ContaineTextShop h1{
  color: #3D5980;
  font-size: 4.4vh;
  margin: 0;
  margin-bottom: 2%;
}
.ContaineTextShop h2{
  color: #FFFFFF;
  font-size: 1.8vh;
  margin: 0;
  text-align: center;
}
.ContaineShopImg{
  width: 50%;
  height: 70%;
  background-image: url("../Image/Shop/user.png");
  background-repeat: no-repeat;
  background-position-x: 50%;
  background-position-y: 50%;
  position: relative;
  margin-left: 7%;
}
.ImgContaine::before{
  display: block;
  content: "";
  width: 35%;
  height: 30%;
  position: absolute;
  border-top:.5vh solid #FFFFFF;
  border-left:.5vh solid #FFFFFF;
  left: -5%;
  top: -5%;
}
.ImgContaine::after{
  content: "";
  display: block;
  width: 35%;
  height: 30%;
  position: absolute;
  border-right:.5vh solid #FFFFFF;
  border-bottom:.5vh solid #FFFFFF;
  right: -5%;
  bottom: -5%;
}
.ImgContaine{
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: .5fr .5fr .5fr;
  align-items: center;
}
.Link{
  text-decoration: none;
}
.Link h3{
  font-size: 3.6vh;
  margin-top: 1%;
  color: #FFFFFF;
  opacity: .6;
  transition: all 2s;
  font-weight: bolder;
}
.Link:hover h1{
  opacity: 1;
}



@media (max-width : 1140px) {
  .ColoneLastMatch::before, .ShopTitle::before , .ShopTitle::after{
    display: none;
  }
  .ShopComponent{
    height: fit-content;
  }
}
@media (min-width : 999px) and (max-width : 1260px) {
  .ColoneMatchs{
    width: 30vw !important;
  }
  .Match{
    width: 30vw !important;
  }
}
@media (min-width : 320px) and (max-width : 485px) {
  /* Styles pour tlphone */

  .FirstLayer:after, .ParagrapheNews::after{
    bottom: 0;
  }
  .TextLine{
    font-size: 4.5vh !important;
  }
  .ParagrapheNews p{
    height: 33vh;
  }
  .ColoneLastMatch, .ColoneNextMatch{
    margin-top: 13%;
    width: 90vw !important;
  }
  .MatchCompTitle::before{
    display: none;
  }
  .RsDiv{
    width: 59% !important;
    margin-bottom: 20%;
    margin-top: 23%;
  }
  .BarreScrollStyle span{
    font-size: 2vh;
  }
  .ContaineTitleAbout h1{
    font-size: 2.7rem;
  }
  .ContaineTitleAbout p{
    width: 100%;
    font-size: .8em;
  }
  .ColoneLastMatch h1, .ColoneNextMatch h1{
    align-self: flex-end !important;
    margin-right: 5%;
  }
  .marquee{
    display: none;
  }




  .ContaineShopImg{
    background-size: 50%;
    background-size: 80% 100%;
  }
  .SecondeImg{
    display: none;
  }
  .ImgContaine{
    grid-template-columns: 1fr !important;
  }
  .ImgContaine img{
    width: 50% !important;
    justify-self: center;
  }
  .ContaineTextShop h1, .ContaineTextShop h2 {
    display: none !important;
  }
  .ContaineTextShop h3{
    font-size: 4.5vh !important;
  }
}
@media (min-width : 485px) and (max-width : 800px) {

  .ColoneLastMatch, .ColoneNextMatch{
    min-width: 470px;
  }
  .MatchCompTitle::before{
    display: none;
  }
