.CarouselContaine{
    align-items: center;
    align-self: flex-start;
    width: 67%;
}
.CarouselShow{
   display: flex;
   width: 100%;
   justify-content: center;
   margin-right: 6%;
   margin-top: .7%;
}
.Carousel{
    position: relative;
    align-items: center;
    width: 100%;
    border: 3px solid #2E506D;
}
.Carousel::after, .Carousel::before, .ArrowContainer::after, .ArrowContainer::before{
    display: block;
    content: "";
    width: 1.4vw;
    height: 1.4vw;
    position: absolute;
    z-index: 2;
}
.Carousel::after{
    bottom: -4px;
    right: -4px;
    border-bottom: 4px solid #02A3CF;
    border-right: 4px solid #02A3CF;
}
.Carousel::before{
    bottom: -4px;
    left: -4px;
    border-left: 4px solid #02A3CF;
    border-bottom: 4px solid #02A3CF;
}
.ArrowContainer::after{
    top: -4px;
    left: -4px;
    border-left: 4px solid #02A3CF;
    border-top: 4px solid #02A3CF;
}
.ArrowContainer::before{
    top: -4px;
    right: -4px;
    border-right: 4px solid #02A3CF;
    border-top: 4px solid #02A3CF;
}
.ImageNews{
    width:100%;
}

.ArrowContainer{
    justify-content: space-between;
    color: white;
    position: absolute;
    width: 100%;
    height: 100%;
}
.CenterVertically{
    display: flex;
    align-items: center;
}
.Arrow{
    height: 100%;
    width: 13%;
    justify-content: center;
    font-size: 2.5vw;
    cursor: pointer;
}
.Arrow:hover{
    backdrop-filter: grayscale(40%);
}
.CarouselBtn{
    width: 24px;
    height: 7px;
    background-color: #3B3B3B;
    margin-right: 1.1%;
    cursor: pointer;
}
.Line2{
    width: 90%;
    margin-top: 1%;
}