body{
    background-color: #101720;
}
html{
    scroll-behavior: smooth;
}
.TeamChoose{
    width: 100vw;
    height: 90vh;
    padding-top: 10vh;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    overflow: hidden;
    cursor: pointer;
}
.Lol::before, .Tft::before, .Tm::before, .Rl::before{
    content: " ";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #101720;
    opacity: .36;
}
.Lol:hover::before, .Tft:hover::before, .Tm:hover::before, .Rl:hover::before{
    opacity: 0;
    transition: all 1.6s;
}
.Lol:hover .TitleJeux, .Tft:hover .TitleJeux, .Tm:hover .TitleJeux, .Rl:hover .TitleJeux, .Lol:hover::after, .Tft:hover::after, .Tm:hover::after, .Rl:hover::after{
    opacity: 1;
    transition: all 2s;
}
.Lol, .Tft, .Tm, .Rl{
    background-repeat: no-repeat;
    background-size: auto 100%;
    position: relative;
    border-left: 2px solid #101720;
    border-right: 2px solid #101720;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 4vh;

    color: #FFFFFF;
}
.TitleJeux{
    width: fit-content;
    margin: 0;
    opacity: 0;
    color: #FFFFFF;
    writing-mode: vertical-rl;
    transform: rotate(180deg);
}
.Lol{
    background-image: url('../../Image/Team/Ryze.jpg');
}
.Tft{
    background-image: url('../../Image/Team/TftWallpaper.jpg');
}
.Tm{
    background-image: url('../../Image/Team/TmWalpaper.jpg');
}
.Rl{
    background-image: url('../../Image/Team/RlWallpaper.jpg');
}
.Lol::after, .Tft::after, .Tm::after, .Rl::after{
    display: block;
    position: absolute;
    opacity: .36;
}
.Lol::after{
    content:url('../../Image/Team/LolLogo.png') ;
    bottom: 45%;
}
.Tft::after{
    content:url('../../Image/Team/TftLogo.png') ;
    bottom: 50%;
}
.Tm::after{
    content:url('../../Image/Team/TmLogo.png') ;
    bottom: 50%;
}
.Rl::after{
    content:url('../../Image/Team/RlLogo.png') ;
    bottom: 45%;
}
.LinkPageTeam{
    text-decoration: none;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    position: absolute;
}

@media (max-width : 1400px){
    .TeamChoose{
        grid-template-columns: 1;
        grid-template-rows: repeat(4,1fr);
        height: fit-content;
    }
}
@media (max-width : 1400px){
    .TeamChoose{
        height: 120vh;
    }
    .TeamChoose{
        grid-template-columns: 100vw;
        grid-template-rows: 30vh 30vh 30vh 30vh;
    }
    .Tm, .Tft, .Lol, .Rl{
        background-size: 100%;
        background-position-y: 30%;
        border: none;
        border-bottom: 2px solid #101720;
    }
    .TitleJeux{
        font-size: 6vh;
        writing-mode: horizontal-tb;
        transform: rotate(0deg);
    }
    .LinkPageTeam{
        justify-content: flex-start;
    }
    .Rl::after{
        bottom: 15%;
    }
}
@media (max-width : 500px){
    .TitleJeux{
        font-size: 5vh;
        width: 100%;
    }
}
@media (max-width : 430px){
    .TitleJeux{
        font-size: 5vh;
        width: 100%;
    }
}

