.FitPlayerPage{
    width: 100vw;
    height: 90vh;
}
.PlayerPage{
    width: 100vw;
    height: 91vh;
    background-color: #101720;
    position: fixed;
    z-index: 5;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.PlayerPage .round{
    transform: rotate(180deg);
    margin-top: 60%;
}
.PlayerFrameImg h1{
    color: #FFFFFF;
    font-size: 9vh;
    margin: 0;
    position: absolute;
    left: 75%;
    top: 13%;
}
.PlayerFrameImg{
    position: relative;
    align-self: center;
    display: flex;

    width: 300px;
    /* max-width: 390px;
    min-width: 300px; */
    padding-left: 12%;
    padding-right: 12%;
    bottom: 0;
}
.PlayerFrameImg div{
    content: " ";
    position: absolute;
    width: 100%;
    height: 58%;
    border: 2.5px solid #114456;
    background-color: rgb(15, 54, 70, .5);
    bottom: 0;
    left: 0;
}
.PlayerFrameImg::before, .PlayerFrameImg::after, .PlayerFrameImg div::before, .PlayerFrameImg div::after{
    content: " ";
    display: block;
    position: absolute;
    width: 1.7vh;
    height: 1.7vh;
    z-index: 1;
}
.PlayerFrameImg::before{
    border-bottom: .5vh solid #02A3CF;
    border-left: .5vh solid #02A3CF;
    bottom: 0;
    left: 0;
}
.PlayerFrameImg::after{
    border-bottom: .5vh solid #02A3CF;
    border-right: .5vh solid #02A3CF;
    bottom: 0;
    right: -5px;
}
.PlayerFrameImg div::before{
    border-top: .5vh solid #02A3CF;
    border-right: .5vh solid #02A3CF;
    top: -2.5px;
    right: -2.5px;
}
.PlayerFrameImg div::after{
    border-top: .5vh solid #02A3CF;
    border-left: .5vh solid #02A3CF;
    top: -2.5px;
    left: -2.5px;
}
.PlayerFrameImg img{
    width: 300px;
    position: relative;
    z-index: 1;
}
.PlayerFrame h2{
