    margin-left: 2%;
    color: #FFFFFF;
    font-size: 2.5vh;
    position: relative;
    align-self: flex-end;
}
.citation span, .citationReverse span, .VideoContain span, .PlayerFrame h2 span, .PlayerFrameImg h1 span{
    color: #14DBF7;
}
.citation::before{
    position: absolute;
    content: " ";
    display: block;
    border-right: 3px solid #212938;
    width: 100%;
    height: 100%;
    top: 8px;
    right: -4px;
}
.citation::after{
    position: absolute;
    content: " ";
    display: block;
    border-right: 5px solid #02A3CF;
    width: 100%;
    height: 73%;
    top: -25px;
}
.citationReverse::after{
    position: absolute;
    content: " ";
    display: block;
    border-left: 5px solid #02A3CF;
    width: 100%;
    height: 73%;
    top: -25px;
    left: 0;
}
.citationReverse::before{
    position: absolute;
    content: " ";
    display: block;
    border-left: 3px solid #212938;
    width: 100%;
    height: 100%;
    top: 8px;
    left: 0px;
}
