.ContactContain{
    width: 100vw;
    height: 90vh;
    padding-top: 10vh;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.ContactCard{
    display: flex;
}
.ContactCard1{
    width: 35%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    position: relative;
}
.ContactCard1 h1{
    margin: 0;
    color: #FFFFFF;
    font-size: 5vh;
}
.ContactCard1 h1 span{
    color: #14DBF7;
}
.ContactCard1 input{
    width: 60%;
    height: 4%;
    border: none;
    border-radius: 13px;
    padding: 3%;
    font-size: 2.5vh;
    background-color: #FFFFFF;
    border: 4px solid #002437;
}
.ContactCard1 textarea{
    width: 60%;
    resize: none;
    height: 34%;
    padding: 3%;
    border-radius: 13px;
    border: none;
    font-size: 2.1vh;
    background-color: #FFFFFF;
    border: 4px solid #002437;
}
.ContactCard1 button{
    width: 40%;
    height: 7%;
    border-radius: 15px;
    border: none;
    font-size: 2.5vh;
}
.ContactCard1 input:focus, .ContactCard1 textarea:focus{
    background-color: #d4eafe;
    border: 4px solid #024480;
    transition: all 1s;
}
.ContactCard1 textarea:valid, .ContactCard1 input:valid{
    background-color: #e2ffe2;
    border: 4px solid #448669;
}
.ContactCard1 textarea, .ContactCard1 input, .ContactCard1 button{
    outline: none;
    position: relative;
    z-index: 5;
}
.ContactCard1::before{
    position: absolute;
    display: block;
    content: ' ';
    width: 3px;
    height: 90%;
    background-color: #023767;
    right: 0;
    top: 5%;
}
.ContactCard1 button:hover{
    background-color: #023767;
    color: #FFFFFF;
    transition: all 2s;
    cursor: pointer;
}
.ContactCard2{
    width:65%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.ContainTextMoving{
    width: 85%;
    height: 80%;
    overflow: hidden;
    background-color: #0995D5;
    border-radius: 10px;
    position: relative;
}
.ContainTextMoving .ImgContainContact{
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 4;
}
.ContainTextMoving img{
    max-width: 250px;
    min-width: 150px;
    width: 30%;
}
.ContainTextMoving h1,.ContainTextMoving h2{
    font-size:10vh;
    clear: both;
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
    margin: 0;
    font-weight: bolder;
    transform: rotate(-30deg);
    transform-origin: 0% 100%;
}
.ContainTextMoving .MargineKc{
    margin-top: 1.5%;
    margin-bottom: 1.5%;
}
.ContainTextMoving h2{
    color: #FBFDFC;
}
.ContainTextMoving h1{
    color: #040D19;
}
.ContactContain::after{
    display: block;
    content: "";
    width: 90%;
    position: absolute;
    border-top: .4vh solid #141F29;
    right: 5%;
    bottom: 0%;
}
.ContactCard{
    width: 100%;
    height: 100%;
}
@media (max-width: 500px) {
    .ContactCard1 input, .ContactCard1 textarea{
        width: 80%;
    }
    .ContactCard1 button{
        min-width: fit-content;
        width: 45% !important;
    }
}
@media (max-width: 1200px) {
    .ContactContain{
        height: 180vh;
    }
    .ContactCard{
        flex-direction: column;
    }
    .ContactCard1{
        width: 100%;
        height: 50%;
    }
    .ContactCard2{
        width: 100%;
        height: 50%;
    }
    .ContactCard1::before{
        display: none;
    }
    .ContactContain::after{
        bottom: -90% !important;
    }
    .ContactCard1 input, .ContactCard1 textarea{
        padding: 2%;
    }
    .ContactCard1 button{
        width: 25%;
    }
}