.Message{
    background-color: #002474;
    height: fit-content;
    text-align: center;
    color: #FFFFFF;
    padding-top: .4%;
    padding-bottom: .4%;
}
.Headers{
    width: 100vw;
    height: 10vh;
    background-color: #101720;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    z-index: 50;
}
.Headers::before{
    display: block;
    content: "";
    width: 10%;
    height: 5%;
    position: absolute;
    z-index: 2;
    border-top: 3.5px solid #14DBF7;
    bottom: -4px;
    right: 1%;
}
.Headers::after{
    display: block;
    content: "";
    width: 14%;
    height: 5%;
    position: absolute;
    z-index: 2;
    border-top: 3.5px solid #FFFFFF;
    bottom: -4px;
    left: 17%;
}
.ImgIconMenu{
    width:3.3%;
    margin-left:2%;
    position:relative;
    min-width: 47px;
}
.ImgIconMenu::before, .ImgIconMenu::after, .ImgIconMenu span::before, .ImgIconMenu span::after{
    display: block;
    content: "";
    width: .7vw;
    height: .7vw;
    position: absolute;
    z-index: 2;
}
.ImgIconMenu::before{
    border-left: 3.3px solid #02A3CF;
    border-bottom: 3.3px solid #02A3CF;
    bottom: 3.5px;
    left: 0;
}
.ImgIconMenu::after{
    border-right: 3.3px solid #02A3CF;
    border-bottom: 3.3px solid #02A3CF;
    bottom: 3.5px;
    right: -14px;
}
.ImgIconMenu span::before{
    border-right: 3.3px solid #02A3CF;
    border-top: 3.3px solid #02A3CF;
    top: 0;
    right: -14px;
}
.ImgIconMenu span::after{
    border-left: 3.3px solid #02A3CF;
    border-top: 3.3px solid #02A3CF;
    top: 0;
    left: 0;
}
.IconMenu{
    width: 100%;
    background-color: #0F3646;
    border: 2px solid #112734;
    position: relative;
    padding: 0 5px 0 5px;
    cursor: pointer;
}
.LogoSponsort1{
    width: 24%;
}
.LogoSponsort2{
    width: 24%;
}
.MainBAck{
    width: 35%;
}
.KarmineLogo{
    width: 100%;
    margin-left: 4%;
    margin-right: .5%;
    margin-top: 4vh;
    min-width: 85px !important;
}
.LogoContaine{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20%;
    margin-left: 2%;
}
.IconMaillots{
    width: 4.5%;
    margin-right: 1.5%;
    border: 1px solid #0F3646;
    border-radius: 50%;
    min-width: 62px;
}
.IconMaillots:hover{
    border-color: #02A3CF;
}
.HeaderButton{
    display: flex;
    width: 33.75%;
    justify-content: space-evenly;
    align-items: center;
    color: #FFFFFF;
    font-family: 'Noto sans';
}
.HeaderButton a{
    text-decoration: none;
}
.HeaderButton span{
    color: #02A3CF;
}
.HeaderButton h1{
    margin: 0;
    font-size: 3.3vh;
    border-bottom: 2.7px solid #24445c;
    opacity: .5;
    color: #FFFFFF;
}
.HeaderButton h1:hover{
    border-color: #02A3CF;
    opacity: 1;
    transition: all .7s ease-in-out;
    cursor: pointer;
}
@media (min-width : 480px) and (max-width : 999px) {
    .MainBAck{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .LogoContaine{
        width: 60%;
        margin: 0;
    }
    .KarmineLogo{
        max-width: 140px;

    }
}
@media (max-width : 999px) {
    .HeaderButton{
        display: none;
    }
}
@media (min-width : 320px) and (max-width : 480px) {
    /* Styles pour tlphone */
    .Headers::before, .Headers::after{
        display: none;
    }
    .Headers{
        height: 9vh;
    }
    .LogoContaine{
        width: 70%;
    }
    .ImgIconMenu{
        width: 10%;
    }
    .IconMaillots{
        width: 15%;
    }
    .LogoContaine .Sponsort{
        display: none;
    }
}



.NavBar{
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: 9;
    background-color: #101720;
    padding-top: 10vh;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-image: url('../Image/Headers/Kc.svg');
    background-repeat: no-repeat;
    animation: fadeIn 1s;
    display: flex;
    flex-direction: column;
}
.ListePage{
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 1.5%;
}
.ListePage .BcKTExt::before, .ListePage .BcKTExt::after, .ListePage h1::before, .ListePage h1::after{
    content: "";
    display: block;
    width: 1.7vh;
    height: 1.7vh;
    position: absolute;
    opacity: .6;
}
.ListePage .BcKTExt::before{
    border-top: .5vh solid #02A3CF;
    border-left: .5vh solid #02A3CF;
    top: 0;
    left: 0;
}
.ListePage .BcKTExt::after{
    border-bottom: .5vh solid #02A3CF;
    border-left: .5vh solid #02A3CF;
    bottom: 0;
    left: 0;
}
.ListePage  h1::before{
    border-top: .5vh solid #02A3CF;
    border-right: .5vh solid #02A3CF;
    top: 0;
    right: 0;
}
.ListePage  h1::after{
    border-bottom: .5vh solid #02A3CF;
    border-right: .5vh solid #02A3CF;
    bottom: 0;
    right: 0;
}
.ListePage h1{
    margin: 0;
    color: #FFFFFF;
    font-size: 7.8vh;
    display: flex;
    padding-left: .7vw;
    padding-right: .7vw;
}
.ListePage div{
    margin-top: 1%;
}
.ListePage a{
    text-decoration: none;
}
.ListePage h1 span, .ListePage ul li h2 span{
    color: #14DBF7;
}
.ListePage .BcKTExt{
    position: relative;
    background-color: rgb(15, 54, 70, .5);
    width: fit-content;
    height: 10vh;
}
.ListePage ul{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    width: 80%;
    justify-content: space-around;
}
.ListePage ul li h2{
    color: #FFFFFF;
    font-size: 4.6vh;
    width: fit-content;
    border-bottom: .5vh solid #24445c;
}
.ListePage ul li{
    animation: fadeIn 1s;
}
.ListePage h2{
    margin: 0;
}
.ListeInnerText{
    display: flex;
}
#LeagueTExt, #TftTExt, #TmTExt, #RlText{
    position: relative;
    border: none;
    font-size: 4vh;
}
#LeagueTExt::before, #TftTExt::before, #TmTExt::before, #RlText::before{
    content: "";
    position: absolute;
    background-repeat: no-repeat;
    z-index: -1;
}
#LeagueTExt::before{
    width: 66px;
    height: 54px;
    background-image: url("../Image/Headers/LogoLol\ 1.png");
    bottom: 75%;
    left: 40%;
}
#TftTExt::before{
    width: 70px;
    height: 70px;
    background-image: url("../Image/Headers/tft.png");
    bottom: 62%;
    left: 40%;
}
#TmTExt::before{
    width: 68px;
    height: 75px;
    background-image: url("../Image/Headers/TMGL_Fall_2020_logo.jpg");
    bottom: 30%;
    left: 40%;
}
#RlText::before{
    width: 70px;
    height: 67px;
    background-image: url("../Image/Headers/Rl.png");
    bottom: 50%;
    left: 40%;
}
@media (max-width : 1180px) {
    .ListePage{
        align-items: center;
    }
    .ListePage .BcKTExt{
        height: 7.5vh;
        margin-top: 1vh;
    }
    .ListePage ul li h2{
        font-size: 4vh;
        text-align: center;
        width: fit-content;
    }
    .ListePage ul li{
        margin: 0;        
    }
    .ListePage ul{
        flex-direction: column;
        width: 100vw;

    }
    .ListeInnerText{
        flex-direction: column;
        align-items: center;
    }
    .ListePage h1{
        font-size: 6vh;
    }
    #LeagueTExt::before, #TftTExt::before, #TmTExt::before, #RlText::before{
        display: none;
    }
}
@keyframes fadeIn {
    0% {
      opacity:0;
      transform: translateX(-20px);
    }
    100% {
      opacity:1;
      transform: translateX(0);
    }
  }