.ContentContain{
    background-color: #101720;
    width: 100vw;
    height: 90vh;
    padding-top: 10vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    position: relative;
}
.TitleContain{
    font-size: 20vh;
    max-width: 750px;
    position: absolute;
    align-self: flex-start;
    color: #2a3c53 !important;
}
.SubTitleContain{
    font-size: 19vh;
    max-width: 720px;
    bottom: 75px;
    right: 2%;
    position: relative;
    align-self: flex-end;
    color: #2a3c53 !important;
}
.ContentContain h1{
    color: #FFFFFF;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0;
}
.ChangersPage{
    font-size: 3vh;
}
.ContentContain h1 span{
    font-size: 2vh;
    cursor: pointer;
}
.ContainListe{
    width: 87%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    row-gap: 20px;
    column-gap: 40px;

    margin-top: 9%;
}
.FigureMiniature{
    position: absolute;
    width: 20%;
    left:-9%;
    top: 0; 
    transform: rotate(-45deg);
}
.FigureMiniatureReverse{
    position: absolute;
    width: 19%;
    left: 91%;
    bottom: 0px; 
    transform: rotate(-225deg);
}

.ImgNews{
    width: 100%;
    position: relative;
    border: 2px solid #FFFFFF;
    z-index: 3;
    position: relative;
}
.content {
    position:relative;
    display: flex;
    align-items: center;
    justify-content: center; 
    max-width: 354px;
}
.content p {
    flex: 1;
    display: flex;
    padding: 0;
}
.content:before, 
.content:after, 
.content p:before, 
.content p:after {
      position: absolute;
      width: 60px; 
      height: 50px;
      border-color: white; 
      border-style: solid;
      content: ' ';
}
.content:before {
      top: 10px;
      left: 10px;
      border-width: 2px 0 0 2px
}
.content:after {
    top: 10px;
    right: 10px;
    border-width: 2px 2px 0 0
}
.content p:before {
    bottom: 10px;
    right: 10px;     
    border-width: 0 2px 2px 0
}
.content p:after {
      bottom: 10px;
      left: 10px;
      border-width: 0 0 2px 2px
}
.TitreNewsPage h1{
    margin: 0;
    font-size: 1.6em;
    color: #FFFFFF;
    border-left: 3.5px solid #FFFFFF;
    border-bottom: 3.5px solid #FFFFFF;
    padding-left: 1.6%;
    padding-right: 5%; 
    display: inline;   
}
.TitreNewsPage h2{
    margin: 0;
    font-size: 1.08em;
    color: #FFFFFF;
    margin-top: 2%;
    padding-left: 2%;
}


.FigureYoutube, .FigureLogo{
    position: absolute;
}
.FigureYoutube{
    width: 22%;
    top: -40px;
    right: -40px;
    transform: rotate(30deg);
}
.FigureLogo{
    width: 23%;
    bottom: -50px;
    left: -42px;
    transform: rotate(-20deg);
}
.YoutubeMinia{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.YoutubeMinia h1{
    color: #000000;
    font-size: 2vh;
    width: 76%;
}
.YoutubeMinia h2{
    font-size: 2.5vh;
    margin-left: 13%;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    width: 70%;
}
.DIvNews{
    display: flex;
    flex-direction: column;
    align-items: center;
}
@media (min-width : 320px) and (max-width : 750px) {
    .ContentContain{
        height: fit-content;
        padding-bottom: 5%;
    }
    .TitleContain{
        position: inherit;
        font-size: 7vh;
        margin-top: 5% !important;
        margin-left: 3% !important;
    }
    .ContainListe{
        grid-template-columns: 1fr;
        grid-template-rows: auto;
        grid-row-gap: 40px;
    }
    .SubTitleContain{
        display: none;
    }
}
@media (min-width : 750px) and (max-width : 1350px) {
    .ChangersPage{
        margin-top: 2%;
    }
    .ContentContain{
        height: fit-content;
        padding-bottom: 3%;
    }
.SubTitleContain{
    display: none;
}
    .ContainListe{
        width: 95%;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(3, 1fr);
    }

}
@media (max-width : 1578px) {
    .SubTitleContain{
        bottom: 30px;
        right: 2%;
    }
}
